<template>
  <div style="width:100%; height:100%; display:block; background:#fff;">
    <mobile-header
      title="회원가입"
      class="join-header"
      right-icon="mdi-help-circle-outline"
      @close="closeJoinPage"
    />
    <div>
      <div id="m_join" v-if="step === 'first'">
        <step-box step="1" />
        <StepDesc desc1="아이디와 비밀번호를" desc2="입력해 주세요" />

        <section class="id-pw__wraps">
          <v-text-field
            v-model="member.id"
            color="main"
            :rules="rules.id"
            placeholder="아이디(4 ~ 13자리 이내)"
            required
          />

          <v-text-field
            v-model="member.memberPw"
            color="main"
            :append-icon="show1 ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
            :type="show1 ? 'text' : 'password'"
            :rules="rules.password"
            placeholder="비밀번호(10 ~ 20자리 이내)"
            required
            @click:append="show1 = !show1"
          />

          <v-text-field
            ref="passwordRe"
            v-model="memberPwRe"
            color="main"
            :append-icon="show1 ? 'mdi-eye-outline' : 'mdi-eye-off-outline'"
            :type="show1 ? 'text' : 'password'"
            placeholder="비밀번호 확인"
            required
            @click:append="show1 = !show1"
          />
        </section>
        <div
          id="next-btn__box"
          :class="{ active: member.id && member.memberPw === memberPwRe }"
        >
          <button @click="validIdPw">
            다음
          </button>
        </div>
      </div>

      <div id="m_join" v-else-if="step === 'second'">
        <StepBox step="2" />
        <StepDesc desc1="등록하실 장례식장의" desc2="정보를 입력해 주세요" />

        <v-text-field
          v-model="member.memberAddr"
          type="text"
          placeholder="장례식장 주소"
          label="주소검색"
          readonly
          append-icon="mdi-map-marker"
          color="main"
          @focus="addrRegPopup"
        />

        <div v-if="member.memberAddr">
          <v-text-field
            v-model="member.memberName"
            type="text"
            color="main"
            label="장례식장명"
            placeholder="장례식장명"
            readonly
            required
          />

          <v-text-field
            v-model="member.memberPhone"
            type="text"
            label="전화번호"
            color="main"
            placeholder="전화번호(숫자만 입력)"
            maxlength="11"
            :rules="rules.onlyNum"
          />
        </div>

        <div id="next-btn__box" class="active">
          <button @click="step = 'third'">
            다음
          </button>
        </div>
      </div>

      <div v-else-if="step === 'third'" id="m_join">
        <StepBox step="3" />
        <StepDesc desc1="사업자등록증을" desc2="업로드해주세요" />
        <button style="width: 100%" ref="fileuploadbtn" @click="fileUpload">
          <v-text-field
            :placeholder="filename !== null ? filename : '사업자등록증 업로드'"
            readonly
            hide-details
            dense
            color="main"
          >
            <template #append>
              <v-icon>
                mdi-tray-arrow-up
              </v-icon>
            </template>
          </v-text-field>
        </button>

        <input
          type="file"
          ref="fileupload"
          @change="changeFiles"
          multiple
          style="display:none;"
        />
        <p class="mt-6 mb-0">
          * 사업자등록증을 업로드하셔야 가입이 완료됩니다.
        </p>
        <div id="next-btn__box" class="active">
          <button @click="submitTerm">
            가입 완료하기
          </button>
        </div>
      </div>
    </div>
    <AddressSearch :method="registAddress" />
    <TermPopup v-if="agreeFlag" :method="submit" />
  </div>
</template>

<script>
import myMixin from "@/mixin/mixin";
import rules from "@/mixin/rules";
import AddressSearch from "@/components/common/AddressSearch";
import TermPopup from "@/components/common/TermPopup";

import MobileHeader from "@/components/common/MobileHeader.vue";
import StepBox from "@/components/common/StepBox.vue";
import StepDesc from "@/components/common/StepDesc.vue";
export default {
  name: "JoinCorp",
  mixins: [myMixin, rules],
  components: {
    AddressSearch,
    TermPopup,
    MobileHeader,
    StepBox,
    StepDesc
  },
  props: {},
  data() {
    return {
      member: {
        id: "",
        memberAddr: "",
        memberPost: "",
        memberAddrX: "",
        memberAddrY: "",
        memberBirth: null,
        memberClass: "MEMBER",
        memberName: "",
        memberPhone: "",
        memberPush: null,
        memberPushYn: "Y",
        memberPw: "",
        memberType: "NORMAL",
        memberCorpMaster: "N",
        memberAttr: {
          codeId: "TYPE-0"
        },
        memberUseYN: {
          codeId: "SUCCESS-1"
        }, //장례식장은 무조건 대기
        memberMarketingAgree: false
      },
      memberPwRe: null,
      files: [],
      filename: null,
      agreeFlag: false,

      step: "first",
      show1: false
    };
  },
  mounted() {},
  methods: {
    closeJoinPage() {
      this.$router.go(-1);
    },

    submitTerm() {
      var regType1 = /^[A-Za-z0-9+]*$/;
      if (!regType1.test(this.member.id)) {
        alert("아이디는 숫자와 영어만 입력 가능합니다.");
        this.step = "first";
        return;
      }
      if (!this.validataion()) {
        return;
      }
      this.$store.commit("START_DEEMED");
      this.agreeFlag = true;
    },
    submit(val) {
      this.agreeFlag = false;
      this.$store.commit("END_DEEMED");
      if (!this.validataion()) {
        return;
      }
      this.member.memberMarketingAgree = val;
      const parameter = {
        member: this.member,
        files: this.files
      };
      this.$store.dispatch("REQUEST_JOIN", parameter);
    },

    validataion() {
      //if(!this.emailCheck(this.member.id)){
      //    return false;
      //}
      if (!(this.member.id.length >= 5 && this.member.id.length <= 12)) {
        alert("정확한 아이디를 입력해 주세요.");
        this.step = "first";
        return false;
      }
      if (
        this.member.memberPw === null ||
        this.member.memberPw === undefined ||
        this.member.memberPw === ""
      ) {
        alert("비밀번호를 입력해 주세요.");
        this.step = "first";
        return false;
      }

      if (this.member.memberPw !== this.memberPwRe) {
        alert("비밀번호확인이 일치하지 않습니다.");
        this.step = "first";
        return false;
      }

      if (
        this.member.memberName === null ||
        this.member.memberName === undefined ||
        this.member.memberName === ""
      ) {
        alert("장례식장명을 입력해 주세요.");
        this.step = "second";
        return false;
      }

      if (
        this.member.memberPhone === null ||
        this.member.memberPhone === undefined ||
        this.member.memberPhone === ""
      ) {
        alert("전화번호를 입력해 주세요.");
        this.step = "second";
        return false;
      }
      if (
        this.member.memberAddr === null ||
        this.member.memberAddr === undefined ||
        this.member.memberAddr === ""
      ) {
        alert("장례식장 주소를 입력해 주세요.");
        this.step = "second";
        return false;
      }
      if (this.files.length < 1) {
        alert("장례식장 인증파일을 업로드해 주세요.");
        this.step = "third";
        return false;
      }

      return true;
    },
    changeFiles(event) {
      console.log(event.target.files[0].name);
      this.filename = event.target.files[0].name;
      this.files = event.target.files;
    },
    fileUpload() {
      this.$refs.fileupload.click();
    },
    addrRegPopup() {
      $("#address-search").addClass("active");
      this.$store.commit("START_DEEMED");
    },
    registAddress(val) {
      this.member.memberName = val.place;
      this.member.memberAddr = val.address;
      this.member.memberPhone = val.phone.replace(/-/gi, "");
      this.member.memberAddrX = val.x;
      this.member.memberAddrY = val.y;
      this.member.memberPost = val.post;
      this.member.memberFuneralAddr = val.memberFuneralAddr;
    },
    validIdPw() {
      if (!this.member.id) return alert("아이디를 입력하여 주십시오");

      if (!this.member.memberPw) return alert("비밀번호를 입력하여 주십시오");

      if (this.member.memberPw !== this.memberPwRe) {
        return alert("비밀번호 확인이 일치하지 않습니다");
      }

      this.step = "second";
    }
  }
};
</script>
