const rules = {
  data() {
    return {
      rules: {
        id: [
          v => (v || "").length > 0 || "아이디를 입력하여 주십시오",
          v => v.length >= 5 || "아이디는 5~12자로 생성하여 주십시오",
          v => v.length <= 12 || "아이디는 5~12자로 생성하여 주십시오",
          v => /^[A-Za-z0-9+]*$/.test(v) || "아이디는 숫자와 영문만 가능합니다"
        ],
        password: [v => (v || "").length > 0 || "비밀번호를 입력하여 주십시오"],
        passwordCheck: [
          v => !!v || "비밀번호 확인은 필수입니다.",
          v => v === this.password || "비밀번호가 일치하지 않습니다."
        ],
        onlyNum: [v => /^[0-9]*$/.test(v) || "숫자만 입력하여 주십시오"],
        address: [v => !!v || "주소를 입력하여 주십시오"],
        phone: [v => /^[0-9]*$/.test(v) || "숫자만 입력하여 주십시오"]
      }
    };
  }
};

export default rules;
