<template>
  <div id="address-search">
    <div class="address-search__inner">
      <header class="px-2">
        <h1 class="py-4">주소검색</h1>

        <v-radio-group v-model="searchType" hide-details class="px-4 pb-0">
          <v-radio
            value="only"
            label="장례식장 검색"
            color="main"
            class="mb-4"
          ></v-radio>
          <v-radio
            value="all"
            label="전체 검색(카카오 지도로 주소검색)"
            color="main"
          ></v-radio>
        </v-radio-group>

        <div class="address-input__wrap mt-4 mr-0">
          <v-text-field
            v-model="address"
            type="text"
            placeholder="주소를 입력하세요"
            flat
            solo
            dense
            color="main"
            hide-details
            class="pt-0"
            @keyup.enter="searchAddress()"
          >
            <template #append>
              <v-icon @click="searchAddress">mdi-magnify</v-icon>
            </template>
          </v-text-field>
        </div>
      </header>

      <div class="address-list__wrap" style="margin-top: 10px">
        <ul>
          <li
            v-for="(address, index) in getAddressSearchList"
            :key="index"
            @click="registAddress(address)"
          >
            <span>{{ addressTxt(address) }}</span>
            <button class="address_register">등록</button>
          </li>
        </ul>

        <button class="btn-address_close" @click="close">닫기</button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "AddressSearch",
  props: ["method"],
  data() {
    return {
      address: "",
      searchType: "only"
    };
  },
  computed: {
    ...mapGetters(["getAddressSearchList"])
  },
  methods: {
    addressTxt(address) {
      debugger;
      if (
        address.road_address_name !== null &&
        address.road_address_name !== undefined
      ) {
        return address.road_address_name + " " + address.place_name;
      } else {
        return address.funeralAddr2 + " " + address.funeralAddrName;
      }
    },
    searchAddress() {
      if (this.searchType === "only") {
        this.$store.dispatch(
          "REQUEST_ADDRESS_SEARCH_FUNERAL_LIST",
          this.address
        );
      } else if (this.searchType === "all") {
        this.$store.dispatch("REQUEST_ADDRESS_SEARCH_LIST", this.address);
      } else {
        this.$store.dispatch("REQUEST_ADDRESS_SEARCH_LIST", this.address);
      }
    },
    registAddress(address) {
      debugger;
      let parameter = {};
      if (
        address.road_address_name !== null &&
        address.road_address_name !== undefined
      ) {
        parameter = {
          address: address.road_address_name,
          place: address.place_name,
          phone: address.phone,
          x: address.x,
          y: address.y,
          funeralAddrCode: null
        };

        const context = this;
        this.$store
          .dispatch("REQUEST_ADDRESS_SEARCH_POST_CODE", parameter)
          .then(data => {
            debugger;
            if (data.result) {
              parameter.post = data.post;
            }

            context.method(parameter);
            context.close();
          });
      } else {
        parameter = {
          address: address.funeralAddr2,
          place: address.funeralAddrName,
          phone: address.funeralAddrPhone,
          x: address.funeralAddrX,
          y: address.funeralAddrY,
          memberFuneralAddr: { id: address.id }
        };
        this.method(parameter);
        this.close();
      }
    },
    close() {
      $("#address-search").removeClass("active");
      this.$store.commit("END_DEEMED");
    }
  }
};
</script>
